// These styles are needed to fix border-radius for .btn-group's
//
// The .btn-group styles reference '.btn-sm' not '.btn-bsk.bsk-sm'.
// This means we need to add some styles for .btn's with our styles which we can extend from our clone of the .btn-group
// styles. We don't want these additional styles ourselves so use a reference stylesheet to prevent their final output.
//
// The originating .btn-group styles extend on the 'base' .btn-* size styles. These extends calls doesn't include the
// modifications we make to the base .btn-* styles as we do so in a wrapper which the .btn-group styles don't reference.
//
// E.g. we make modifications like this:
/*
.btn-bsk {
  &.btn-sm {
    // border-radius
  }
}
*/

// Button Sizes
// --------------------------------------------------

.btn-lg-bsk {
  // line-height: ensure even-numbered height of button next to large input
  .button-size(@padding-large-vertical; @padding-large-horizontal; @font-size-large; @line-height-large; @btn-bsk-border-radius);
}
&.btn-sm-bsk {
  // line-height: ensure proper height of button next to small input
  .button-size(@padding-small-vertical; @padding-small-horizontal; @font-size-small; @line-height-small; @btn-bsk-border-radius);
}
&.btn-xs-bsk {
  .button-size(@padding-xs-vertical; @padding-xs-horizontal; @font-size-small; @line-height-small; @btn-bsk-border-radius);
}
