// Navbar large vertical align
//
// Vertically center elements in a large navbar.
// Example: an element has a height of 30px, so write out `.navbar-lg-vertical-align(30px);` to calculate the appropriate top margin.

.navbar-lg-vertical-align(@element-height) {
  margin-top: (((@navbar-bsk-lg-height - @element-height) / 2) - 2);
  margin-bottom: (((@navbar-bsk-lg-height - @element-height) / 2) - 2);
}
